<template>
  <v-app>
    <v-main>
      <div v-if="targetForm">
        <p>Will Be A Form</p>
      </div>
      <div v-else>
        <Container />
      </div>
    </v-main>
  </v-app>
</template>

<script>
import Container from "./components/Container";

export default {
  name: "App",

  components: {
    Container,
  },

  created: function() {
    console.log(`Query is: ${window.location.search}`);
    const params = new URLSearchParams(window.location.search);
    const target = params.get("form");
    console.log(`Target is: ${target}`);
    if (target) {
      this.targetForm = target;
    }
  },

  data: () => ({
    targetForm: "",
  }),
};
</script>
